<template>
  <div class="investment-detail-page">
    <header class="page-header">
      <h1>üìà Ìà¨Ïûê ÏÉÅÌíà ÏÉÅÏÑ∏ Ï†ïÎ≥¥</h1>
      <p class="description" v-if="investment">
        "{{ investment.productName }}" ÏÉÅÌíàÏùò ÏÉÅÏÑ∏ ÌòÑÌô©ÏûÖÎãàÎã§.
      </p>
      <p class="description" v-else>
        Ìà¨Ïûê ÏÉÅÌíàÏùò ÏÉÅÏÑ∏ Ï†ïÎ≥¥Î•º Î∂àÎü¨Ïò§Îäî Ï§ëÏûÖÎãàÎã§.
      </p>
    </header>

    <main class="content-wrapper">
      <div v-if="loading" class="loading-state">
        <div class="spinner"></div>
        <p>ÏÉÅÏÑ∏ Ï†ïÎ≥¥Î•º Î∂àÎü¨Ïò§Îäî Ï§ëÏûÖÎãàÎã§...</p>
      </div>

      <div v-else-if="error" class="error-state">
        <p class="error-message">
          üòî ÏÉÅÏÑ∏ Ï†ïÎ≥¥Î•º Î∂àÎü¨Ïò§Îäî Îç∞ Ïã§Ìå®ÌñàÏäµÎãàÎã§: {{ error }}
        </p>
        <button @click="fetchInvestmentDetails" class="retry-button">
          Îã§Ïãú ÏãúÎèÑ
        </button>
      </div>

      <div v-else-if="!investment" class="not-found-state">
        <p>ÏöîÏ≤≠ÌïòÏã† Ìà¨Ïûê ÏÉÅÌíàÏùÑ Ï∞æÏùÑ Ïàò ÏóÜÏäµÎãàÎã§.</p>
        <router-link to="/my-investments" class="back-button"
          >ÎÇ¥ Ìà¨Ïûê Î™©Î°ùÏúºÎ°ú ÎèåÏïÑÍ∞ÄÍ∏∞</router-link
        >
      </div>

      <div v-else class="detail-card card glassmorphism">
        <div class="card-section header-section">
          <h2>{{ investment.productName }}</h2>
          <span :class="['status-badge', investment.status]">{{
            getStatusText(investment.status)
          }}</span>
        </div>

        <div class="card-section summary-section">
          <div class="summary-item">
            <span class="label">Ìà¨Ïûê Í∏àÏï°</span>
