            <span class="value"
              >{{ investment.currentValue.toLocaleString() }} Ïõê</span
            >
          </div>
          <div class="summary-item">
            <span class="label">ÏàòÏùµÎ•†</span>
            <span :class="['value', getReturnRateClass(investment.returnRate)]"
              >{{ investment.returnRate.toFixed(2) }} %</span
            >
          </div>
