<script>
import { ref, onMounted } from "vue";
import { useRoute } from "vue-router"; // useRouter Ï†úÍ±∞
import { db } from "@/firebaseConfig";
import { doc, getDoc } from "firebase/firestore";

export default {
  name: "InvestmentDetailPage",
  setup() {
    const route = useRoute();
    // const router = useRouter(); // Ïù¥ Ï§ÑÏùÑ Ï†úÍ±∞ÌïòÍ±∞ÎÇò Ï£ºÏÑù Ï≤òÎ¶¨ÌïòÏÑ∏Ïöî.
    const investment = ref(null);
    const loading = ref(true);
    const error = ref(null);

    const fetchInvestmentDetails = async () => {
      loading.value = true;
      error.value = null;
      try {
        const investmentId = route.params.id; // URL ÌååÎùºÎØ∏ÌÑ∞ÏóêÏÑú ID Í∞ÄÏ†∏Ïò§Í∏∞
        if (!investmentId) {
          error.value = "Ìà¨Ïûê IDÍ∞Ä Ï†úÍ≥µÎêòÏßÄ ÏïäÏïòÏäµÎãàÎã§.";
          loading.value = false;
          // ÎßåÏïΩ Ïó¨Í∏∞ÏÑú Í∞ïÏ†úÎ°ú ÎåÄÏãúÎ≥¥ÎìúÎÇò Îã§Î•∏ ÌéòÏù¥ÏßÄÎ°ú Ïù¥ÎèôÏãúÌÇ§Í≥† Ïã∂Îã§Î©¥
          // router.push('/my-investments'); // Ïù¥Î†áÍ≤å useRouterÎ•º ÏÇ¨Ïö©Ìï¥Ïïº Ìï©ÎãàÎã§.
          return;
        }

        const investmentDocRef = doc(db, "investments", investmentId);
        const investmentDocSnap = await getDoc(investmentDocRef);

        if (investmentDocSnap.exists()) {
          investment.value = {
            id: investmentDocSnap.id,
            ...investmentDocSnap.data(),
          };
        } else {
          investment.value = null; // Î¨∏ÏÑúÍ∞Ä Ï°¥Ïû¨ÌïòÏßÄ ÏïäÏùå
        }
      } catch (err) {
        console.error("Ìà¨Ïûê ÏÉÅÏÑ∏ Ï†ïÎ≥¥Î•º Î∂àÎü¨Ïò§Îäî Îç∞ Ïã§Ìå®ÌñàÏäµÎãàÎã§:", err);
        error.value = err.message || "Ïïå Ïàò ÏóÜÎäî Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.";
      } finally {
        loading.value = false;
      }
    };

    const getStatusText = (status) => {
      switch (status) {
        case "active":
          return "ÏßÑÌñâ Ï§ë";
        case "completed":
          return "ÏôÑÎ£åÎê®";
        case "pending":
          return "ÏäπÏù∏ ÎåÄÍ∏∞ Ï§ë";
        case "cancelled":
          return "Ï∑®ÏÜåÎê®";
        default:
          return "Ïïå Ïàò ÏóÜÏùå";
      }
    };

    const getReturnRateClass = (rate) => {
      if (rate > 0) return "positive";
      if (rate < 0) return "negative";
      return "";
    };

    const formatDate = (dateInput) => {
      if (!dateInput) return "";
      // Firebase Timestamp Í∞ùÏ≤¥ Ï≤òÎ¶¨
      if (dateInput.toDate) {
        return dateInput.toDate().toLocaleDateString("ko-KR");
      }
      // JavaScript Date Í∞ùÏ≤¥ Ï≤òÎ¶¨
      if (dateInput instanceof Date) {
        return dateInput.toLocaleDateString("ko-KR");
      }
      return String(dateInput); // Í∑∏ Ïô∏Ïùò Í≤ΩÏö∞ Î¨∏ÏûêÏó¥Î°ú Î≥ÄÌôò
    };

    onMounted(() => {
      fetchInvestmentDetails();
    });

    return {
      investment,
      loading,
      error,
      getStatusText,
      getReturnRateClass,
      formatDate,
      fetchInvestmentDetails,
    };
  },
};
</script>

